\r\n      color: white;\r\n    }\r\n  }\r\n  \r\n  .toggle-icon {\r\n    transition: transform 0.2s;\r\n    color: rgba(255, 255, 255, 0.4);\r\n    \r\n    &.rotated {\r\n      transform: rotate(180deg);\r\n    }\r\n  }\r\n}\r\n\r\n.quest-card {\r\n  position: relative;\r\n  padding: 16px;\r\n  border-radius: 14px;\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n  margin-bottom: 12px;\r\n  overflow: hidden;\r\n  \r\n  &.main {\r\n    background: linear-gradient(135deg, rgba(102, 126, 234, 0.2), rgba(118, 75, 162, 0.1));\r\n    border-color: rgba(102, 126, 234, 0.3);\r\n  }\r\n  \r\n  &.side {\r\n    background: rgba(255, 255, 255, 0.03);\r\n    \r\n    &.completed {\r\n      opacity: 0.6;\r\n    }\r\n  }\r\n}\r\n\r\n.quest-badge {\r\n  position: absolute;\r\n  top: 12px;\r\n  right: 12px;\r\n  padding: 3px 8px;\r\n  background: linear-gradient(135deg, #667eea, #764ba2);\r\n  border-radius: 6px;\r\n  font-size: 9px;\r\n  font-weight: 700;\r\n  color: white;\r\n  letter-spacing: 1px;\r\n}\r\n\r\n.quest-type-badge {\r\n  position: absolute;\r\n  top: 12px;\r\n  right: 12px;\r\n  padding: 3px 8px;\r\n  border-radius: 6px;\r\n  font-size: 10px;\r\n  font-weight: 600;\r\n  \r\n  &.type-daily {\r\n    background: rgba(96, 165, 250, 0.2);\r\n    color: #93c5fd;\r\n  }\r\n  \r\n  &.type-special {\r\n    background: rgba(167, 139, 250, 0.2);\r\n    color: #c4b5fd;\r\n  }\r\n  \r\n  &.type-timed {\r\n    background: rgba(251, 191, 36, 0.2);\r\n    color: #fcd34d;\r\n  }\r\n  \r\n  &.type-hidden {\r\n    background: rgba(248, 113, 113, 0.2);\r\n    color: #fca5a5;\r\n  }\r\n}\r\n\r\n.quest-content {\r\n  padding-right: 60px;\r\n}\r\n\r\n.quest-title {\r\n  font-size: 15px;\r\n  font-weight: 600;\r\n  color: white;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.quest-desc {\r\n  font-size: 12px;\r\n  color: rgba(255, 255, 255, 0.6);\r\n  line-height: 1.5;\r\n  margin-bottom: 12px;\r\n}\r\n\r\n.quest-meta {\r\n  display: flex;\r\n  gap: 12px;\r\n  margin-bottom: 12px;\r\n}\r\n\r\n.meta-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n  font-size: 11px;\r\n  padding: 4px 10px;\r\n  border-radius: 12px;\r\n  \r\n  &.status {\r\n    &.active {\r\n      background: rgba(52, 211, 153, 0.15);\r\n      color: #6ee7b7;\r\n    }\r\n    \r\n    &.completed {\r\n      background: rgba(96, 165, 250, 0.15);\r\n      color: #93c5fd;\r\n    }\r\n    \r\n    &.failed {\r\n      background: rgba(248, 113, 113, 0.15);\r\n      color: #fca5a5;\r\n    }\r\n    \r\n    &.abandoned {\r\n      background: rgba(156, 163, 175, 0.15);\r\n      color: #d1d5db;\r\n    }\r\n  }\r\n  \r\n  &.deadline {\r\n    background: rgba(251, 191, 36, 0.15);\r\n    color: #fcd34d;\r\n  }\r\n}\r\n\r\n.quest-reward {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  padding: 10px 12px;\r\n  background: rgba(251, 191, 36, 0.1);\r\n  border-radius: 10px;\r\n  font-size: 12px;\r\n  color: #fcd34d;\r\n  margin-bottom: 12px;\r\n  \r\n  i {\r\n    color: #fbbf24;\r\n  }\r\n  \r\n  &.small {\r\n    padding: 4px 10px;\r\n    font-size: 11px;\r\n    background: transparent;\r\n    margin-bottom: 0;\r\n  }\r\n}\r\n\r\n.quest-objectives {\r\n  padding-top: 12px;\r\n  border-top: 1px solid rgba(255, 255, 255, 0.08);\r\n}\r\n\r\n.objectives-title {\r\n  font-size: 11px;\r\n  color: rgba(255, 255, 255, 0.5);\r\n  margin-bottom: 10px;\r\n  text-transform: uppercase;\r\n  letter-spacing: 0.5px;\r\n}\r\n\r\n.objective-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 8px;\r\n}\r\n\r\n.objective-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  font-size: 12px;\r\n  \r\n  i {\r\n    font-size: 12px;\r\n    \r\n    &.completed {\r\n      color: #34d399;\r\n    }\r\n    \r\n    &.pending {\r\n      color: rgba(255, 255, 255, 0.3);\r\n    }\r\n  }\r\n  \r\n  .obj-key {\r\n    color: rgba(255, 255, 255, 0.8);\r\n  }\r\n  \r\n  .obj-value {\r\n    margin-left: auto;\r\n    color: rgba(255, 255, 255, 0.5);\r\n    font-family: 'JetBrains Mono', monospace;\r\n  }\r\n}\r\n\r\n.quest-footer {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.quest-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 10px;\r\n}\r\n\r\n.completed-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 8px;\r\n}\r\n\r\n.completed-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n  padding: 10px 14px;\r\n  background: rgba(255, 255, 255, 0.02);\r\n  border-radius: 10px;\r\n  font-size: 12px;\r\n  color: rgba(255, 255, 255, 0.5);\r\n  \r\n  i {\r\n    color: #34d399;\r\n    font-size: 14px;\r\n  }\r\n}\r\n\r\n.empty-state {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  padding: 60px 20px;\r\n  text-align: center;\r\n  \r\n  .empty-icon {\r\n    width: 80px;\r\n    height: 80px;\r\n    border-radius: 50%;\r\n    background: rgba(255, 255, 255, 0.03);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-bottom: 20px;\r\n    \r\n    i {\r\n      font-size: 32px;\r\n      color: rgba(255, 255, 255, 0.15);\r\n    }\r\n  }\r\n  \r\n  .empty-title {\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    color: rgba(255, 255, 255, 0.5);\r\n    margin-bottom: 6px;\r\n  }\r\n  \r\n  .empty-desc {\r\n    font-size: 13px;\r\n    color: rgba(255, 255, 255, 0.3);\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
