AAA,aAAA,CAGF,oCACE,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,QAAA,CACA,kBAAA,CAEA,yCAAA,wBAAA,CAAA,cAAA,CAEA,2CACE,UAAA,CACA,WAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,8BAAA,CACA,oCAAA,CACA,iBAAA,CACA,UAAA,CACA,cAAA,CACA,cAAA,CAEA,iDACE,8BAAA,CAIJ,8CACE,cAAA,CACA,eAAA,CACA,UAAA,CACA,cAAA,CACA,iBAAA,CAIJ,gCACE,YAAA,CACA,6BAAA,CACA,kBAAA,CACA,iBAAA,CACA,8BAAA,CACA,kBAAA,CAEA,qCAAA,wBAAA,CAAA,cAAA,CAEA,6CACE,YAAA,CACA,kBAAA,CACA,OAAA,CACA,cAAA,CACA,eAAA,CACA,aAAA,CAEA,+CAAA,cAAA,CAIJ,+BACE,YAAA,CACA,QAAA,CACA,iBAAA,CACA,wCAAA,CAGF,2DACE,MAAA,CACA,YAAA,CACA,kBAAA,CACA,cAAA,CACA,eAAA,CACA,cAAA,CACA,kBAAA,CAGF,6BACE,8BAAA,CACA,oCAAA,CACA,wBAAA,CAEA,mCACE,8BAAA,CACA,UAAA,CAIJ,8BACE,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,OAAA,CACA,kDAAA,CACA,WAAA,CACA,UAAA,CAEA,mDACE,qBAAA,CACA,0CAAA,CAGF,uCACE,UAAA,CACA,kBAAA,CAIJ,mCACE,aAAA,CAEA,qCAAA,aAAA","sourcesContent":["\r\n.shop-page {\r\n  position: relative;\r\n  padding: 16px 20px;\r\n  overflow-y: auto;\r\n  flex: 1;\r\n}\r\n\r\n.shop-header {\r\n  margin-bottom: 16px;\r\n  position: relative;\r\n}\r\n\r\n.special-battle-unlock {\r\n  position: absolute;\r\n  top: 0;\r\n  right: 0;\r\n  width: 20px;\r\n  height: 20px;\r\n  border: none;\r\n  background: transparent;\r\n  opacity: 0.08;\r\n  cursor: pointer;\r\n  transition: opacity 0.2s;\r\n}\r\n\r\n.special-battle-unlock:hover {\r\n  opacity: 0.2;\r\n}\r\n\r\n.gold-card {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 14px;\r\n  padding: 16px 20px;\r\n  background: linear-gradient(135deg, rgba(251, 191, 36, 0.25), rgba(251, 191, 36, 0.08));\r\n  border: 1px solid rgba(251, 191, 36, 0.4);\r\n  border-radius: 16px;\r\n  position: relative;\r\n  backdrop-filter: blur(10px);\r\n  \r\n  > i {\r\n    font-size: 28px;\r\n    color: #fbbf24;\r\n  }\r\n  \r\n  .gold-info {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 2px;\r\n  }\r\n  \r\n  .gold-label {\r\n    font-size: 12px;\r\n    color: rgba(255, 255, 255, 0.6);\r\n  }\r\n  \r\n  .gold-value {\r\n    font-size: 26px;\r\n    font-weight: 700;\r\n    color: #fcd34d;\r\n  }\r\n}\r\n\r\n.category-tabs {\r\n  display: flex;\r\n  gap: 8px;\r\n  margin-bottom: 16px;\r\n  overflow-x: auto;\r\n  padding-bottom: 4px;\r\n  \r\n  &::-webkit-scrollbar {\r\n    height: 3px;\r\n  }\r\n}\r\n\r\n.category-tab {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n  padding: 10px 16px;\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n  border-radius: 10px;\r\n  color: rgba(255, 255, 255, 0.6);\r\n  font-size: 13px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n  white-space: nowrap;\r\n  \r\n  i { font-size: 14px; }\r\n  \r\n  &:hover {\r\n    background: rgba(255, 255, 255, 0.08);\r\n    color: rgba(255, 255, 255, 0.8);\r\n  }\r\n  \r\n  &.active {\r\n    background: linear-gradient(135deg, #667eea, #764ba2);\r\n    border-color: transparent;\r\n    color: white;\r\n  }\r\n}\r\n\r\n.filter-bar {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 10px;\r\n  padding: 12px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 12px;\r\n  margin-bottom: 16px;\r\n}\r\n\r\n.filter-group {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.filter-label {\r\n  font-size: 11px;\r\n  color: rgba(255, 255, 255, 0.5);\r\n  min-width: 36px;\r\n}\r\n\r\n.filter-options {\r\n  display: flex;\r\n  gap: 6px;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.filter-btn {\r\n  padding: 4px 10px;\r\n  font-size: 11px;\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n  border-radius: 6px;\r\n  color: rgba(255, 255, 255, 0.6);\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n  \r\n  &:hover {\r\n    background: rgba(255, 255, 255, 0.1);\r\n    color: white;\r\n  }\r\n  \r\n  &.active {\r\n    color: white;\r\n    \r\n    &.grade-c { background: rgba(156, 163, 175, 0.4); border-color: #9ca3af; }\r\n    &.grade-b { background: rgba(96, 165, 250, 0.4); border-color: #60a5fa; }\r\n    &.grade-a { background: rgba(167, 139, 250, 0.4); border-color: #a78bfa; }\r\n    &.grade-s { background: rgba(251, 191, 36, 0.4); border-color: #fbbf24; }\r\n    &.grade-ss { background: rgba(244, 114, 182, 0.4); border-color: #f472b6; }\r\n  }\r\n  \r\n  &.slot-btn.active {\r\n    background: rgba(102, 126, 234, 0.4);\r\n    border-color: #667eea;\r\n  }\r\n}\r\n\r\n.empty-filter {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 8px;\r\n  padding: 30px;\r\n  color: rgba(255, 255, 255, 0.4);\r\n  \r\n  i { font-size: 24px; }\r\n  span { font-size: 13px; }\r\n}\r\n\r\n.shop-content {\r\n  min-height: 200px;\r\n}\r\n\r\n.item-section {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 12px;\r\n}\r\n\r\n.sub-category {\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.item-meta {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n}\r\n\r\n.item-grade {\r\n  font-size: 10px;\r\n  padding: 1px 5px;\r\n  border-radius: 3px;\r\n  font-weight: 600;\r\n  \r\n  &.grade-c { background: rgba(156, 163, 175, 0.3); color: #d1d5db; }\r\n  &.grade-b { background: rgba(96, 165, 250, 0.3); color: #93c5fd; }\r\n  &.grade-a { background: rgba(167, 139, 250, 0.3); color: #c4b5fd; }\r\n  &.grade-s { background: rgba(251, 191, 36, 0.3); color: #fcd34d; }\r\n  &.grade-ss { background: rgba(244, 114, 182, 0.3); color: #f9a8d4; }\r\n}\r\n\r\n.sub-title {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  color: rgba(255, 255, 255, 0.7);\r\n  margin-bottom: 12px;\r\n  \r\n  i { font-size: 14px; }\r\n}\r\n\r\n.grade-badge, .tier-badge {\r\n  font-size: 11px;\r\n  padding: 2px 8px;\r\n  border-radius: 4px;\r\n  font-weight: 600;\r\n}\r\n\r\n.grade-c .grade-badge { background: rgba(156, 163, 175, 0.3); color: #d1d5db; }\r\n.grade-b .grade-badge { background: rgba(96, 165, 250, 0.3); color: #93c5fd; }\r\n.grade-a .grade-badge { background: rgba(167, 139, 250, 0.3); color: #c4b5fd; }\r\n.grade-s .grade-badge { background: rgba(251, 191, 36, 0.3); color: #fcd34d; }\r\n.grade-ss .grade-badge { background: rgba(244, 114, 182, 0.3); color: #f9a8d4; }\r\n\r\n.tier-basic .tier-badge { background: rgba(156, 163, 175, 0.3); color: #d1d5db; }\r\n.tier-fine .tier-badge { background: rgba(96, 165, 250, 0.3); color: #93c5fd; }\r\n.tier-precious .tier-badge { background: rgba(167, 139, 250, 0.3); color: #c4b5fd; }\r\n.tier-special .tier-badge { background: rgba(251, 191, 36, 0.3); color: #fcd34d; }\r\n\r\n.item-grid {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 8px;\r\n}\r\n\r\n.shop-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 12px;\r\n  padding: 12px 14px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 12px;\r\n  border: 1px solid rgba(255, 255, 255, 0.08);\r\n  cursor: pointer;\r\n  transition: all 0.2s;\r\n  position: relative;\r\n  \r\n  &:hover {\r\n    background: rgba(255, 255, 255, 0.06);\r\n    transform: translateX(4px);\r\n  }\r\n  \r\n  &.grade-c { border-left: 3px solid #9ca3af; }\r\n  &.grade-b { border-left: 3px solid #60a5fa; }\r\n  &.grade-a { border-left: 3px solid #a78bfa; }\r\n  &.grade-s { border-left: 3px solid #fbbf24; }\r\n  &.grade-ss { border-left: 3px solid #f472b6; }\r\n  \r\n  &.tier-basic { border-left: 3px solid #9ca3af; }\r\n  &.tier-fine { border-left: 3px solid #60a5fa; }\r\n  &.tier-precious { border-left: 3px solid #a78bfa; }\r\n  &.tier-special { border-left: 3px solid #fbbf24; }\r\n}\r\n\r\n.item-icon {\r\n  width: 40px;\r\n  height: 40px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border-radius: 10px;\r\n  \r\n  i {\r\n    font-size: 18px;\r\n    color: rgba(255, 255, 255, 0.7);\r\n  }\r\n}\r\n\r\n.item-info {\r\n  flex: 1;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 2px;\r\n  min-width: 0;\r\n}\r\n\r\n.item-name {\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  color: white;\r\n  white-space: nowrap;\r\n  overflow: hidden;\r\n  text-overflow: ellipsis;\r\n}\r\n\r\n.item-type, .item-effect {\r\n  font-size: 11px;\r\n  color: rgba(255, 255, 255, 0.5);\r\n}\r\n\r\n.item-price {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 4px;\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  color: #fcd34d;\r\n  \r\n  i { font-size: 11px; }\r\n  \r\n  .original-price {\r\n    text-decoration: line-through;\r\n    opacity: 0.5;\r\n    font-size: 11px;\r\n    color: #9ca3af;\r\n  }\r\n  \r\n  .discounted-price {\r\n    color: #4ade80;\r\n    font-weight: 700;\r\n  }\r\n  \r\n  .discount-tag {\r\n    background: linear-gradient(135deg, #22c55e, #16a34a);\r\n    color: white;\r\n    font-size: 9px;\r\n    padding: 2px 5px;\r\n    border-radius: 4px;\r\n    font-weight: 700;\r\n    margin-left: 4px;\r\n  }\r\n}\r\n\r\n.combat-tag {\r\n  position: absolute;\r\n  top: 6px;\r\n  right: 6px;\r\n  font-size: 9px;\r\n  padding: 2px 6px;\r\n  background: rgba(239, 68, 68, 0.3);\r\n  color: #f87171;\r\n  border-radius: 4px;\r\n}\r\n\r\n/*  */\r\n.purchase-modal {\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: rgba(0, 0, 0, 0.7);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 1000;\r\n  padding: 20px;\r\n}\r\n\r\n.modal-content {\r\n  width: 100%;\r\n  max-width: 340px;\r\n  background: linear-gradient(180deg, #1e1e2e, #151520);\r\n  border-radius: 20px;\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n  overflow: hidden;\r\n}\r\n\r\n.modal-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 16px 20px;\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.08);\r\n  \r\n  h3 {\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    color: white;\r\n    margin: 0;\r\n  }\r\n  \r\n  .close-btn {\r\n    width: 32px;\r\n    height: 32px;\r\n    display: flex;\r\n    align-items