m-count {\r\n    margin-left: auto;\r\n    font-size: 11px;\r\n    color: rgba(255, 255, 255, 0.4);\r\n    font-weight: 400;\r\n  }\r\n}\r\n\r\n.filter-tabs {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 8px;\r\n  margin-top: 12px;\r\n}\r\n\r\n.filter-group {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  flex-wrap: wrap;\r\n}\r\n\r\n.filter-label {\r\n  font-size: 11px;\r\n  color: rgba(255, 255, 255, 0.5);\r\n  font-weight: 500;\r\n  min-width: 40px;\r\n}\r\n\r\n.filter-btn {\r\n  padding: 4px 12px;\r\n  border-radius: 8px;\r\n  font-size: 11px;\r\n  font-weight: 500;\r\n  background: rgba(255, 255, 255, 0.05);\r\n  border: 1px solid rgba(255, 255, 255, 0.1);\r\n  color: rgba(255, 255, 255, 0.6);\r\n  cursor: pointer;\r\n  transition: all 0.2s ease;\r\n  \r\n  &:hover {\r\n    background: rgba(255, 255, 255, 0.1);\r\n    color: rgba(255, 255, 255, 0.8);\r\n  }\r\n  \r\n  &.active {\r\n    background: linear-gradient(135deg, #667eea, #764ba2);\r\n    border-color: #667eea;\r\n    color: white;\r\n  }\r\n}\r\n\r\n.equipment-grid {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 10px;\r\n}\r\n\r\n.equipment-slot {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 12px;\r\n  padding: 12px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 12px;\r\n  border: 1px solid rgba(255, 255, 255, 0.08);\r\n  transition: all 0.2s ease;\r\n  position: relative;\r\n  \r\n  &:hover {\r\n    background: rgba(255, 255, 255, 0.05);\r\n  }\r\n  \r\n  &.empty {\r\n    opacity: 0.5;\r\n  }\r\n  \r\n  .slot-icon {\r\n    width: 36px;\r\n    height: 36px;\r\n    border-radius: 10px;\r\n    background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.1));\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    flex-shrink: 0;\r\n    \r\n    i {\r\n      font-size: 16px;\r\n      color: #a78bfa;\r\n    }\r\n  }\r\n  \r\n  .slot-info {\r\n    flex: 1;\r\n    min-width: 0;\r\n  }\r\n  \r\n  .slot-label {\r\n    font-size: 10px;\r\n    color: rgba(255, 255, 255, 0.4);\r\n    text-transform: uppercase;\r\n    letter-spacing: 0.5px;\r\n  }\r\n  \r\n  .slot-value {\r\n    font-size: 14px;\r\n    color: white;\r\n    font-weight: 500;\r\n    margin-top: 2px;\r\n    white-space: nowrap;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n  }\r\n  \r\n  .slot-level {\r\n    font-size: 10px;\r\n    padding: 2px 6px;\r\n    border-radius: 6px;\r\n    margin-top: 4px;\r\n    display: inline-block;\r\n    font-weight: 600;\r\n    \r\n    &.rarity-c {\r\n      background: rgba(156, 163, 175, 0.2);\r\n      color: #d1d5db;\r\n    }\r\n    \r\n    &.rarity-b {\r\n      background: rgba(96, 165, 250, 0.2);\r\n      color: #93c5fd;\r\n    }\r\n    \r\n    &.rarity-a {\r\n      background: rgba(167, 139, 250, 0.2);\r\n      color: #c4b5fd;\r\n    }\r\n    \r\n    &.rarity-s {\r\n      background: rgba(251, 191, 36, 0.2);\r\n      color: #fcd34d;\r\n    }\r\n    \r\n    &.rarity-ss {\r\n      background: rgba(248, 113, 113, 0.2);\r\n      color: #fca5a5;\r\n    }\r\n  }\r\n  \r\n  .unequip-btn {\r\n    width: 28px;\r\n    height: 28px;\r\n    border-radius: 8px;\r\n    background: rgba(248, 113, 113, 0.2);\r\n    border: 1px solid rgba(248, 113, 113, 0.3);\r\n    color: #f87171;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    transition: all 0.2s ease;\r\n    flex-shrink: 0;\r\n    \r\n    &:hover {\r\n      background: rgba(248, 113, 113, 0.3);\r\n      transform: scale(1.1);\r\n    }\r\n    \r\n    i {\r\n      font-size: 12px;\r\n    }\r\n  }\r\n}\r\n\r\n.bonus-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(2, 1fr);\r\n  gap: 8px;\r\n}\r\n\r\n.bonus-item {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 10px 12px;\r\n  background: rgba(255, 255, 255, 0.02);\r\n  border-radius: 10px;\r\n  border: 1px solid rgba(255, 255, 255, 0.05);\r\n}\r\n\r\n.bonus-label {\r\n  font-size: 12px;\r\n  color: rgba(255, 255, 255, 0.6);\r\n}\r\n\r\n.bonus-value {\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  font-family: 'JetBrains Mono', monospace;\r\n  \r\n  &.positive {\r\n    color: #34d399;\r\n  }\r\n  \r\n  &.negative {\r\n    color: #f87171;\r\n  }\r\n  \r\n  &.neutral {\r\n    color: rgba(255, 255, 255, 0.4);\r\n  }\r\n}\r\n\r\n.inventory-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 10px;\r\n}\r\n\r\n.inventory-item {\r\n  padding: 14px;\r\n  background: rgba(255, 255, 255, 0.02);\r\n  border-radius: 12px;\r\n  border-left: 3px solid;\r\n  transition: all 0.2s ease;\r\n  position: relative;\r\n  \r\n  &:hover {\r\n    background: rgba(255, 255, 255, 0.05);\r\n    transform: translateX(4px);\r\n  }\r\n  \r\n  &.rarity-c {\r\n    border-left-color: #9ca3af;\r\n  }\r\n  \r\n  &.rarity-b {\r\n    border-left-color: #60a5fa;\r\n  }\r\n  \r\n  &.rarity-a {\r\n    border-left-color: #a78bfa;\r\n  }\r\n  \r\n  &.rarity-s {\r\n    border-left-color: #fbbf24;\r\n  }\r\n  \r\n  &.rarity-ss {\r\n    border-left-color: #f87171;\r\n  }\r\n  \r\n  .item-header {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    margin-bottom: 6px;\r\n  }\r\n  \r\n  .item-name {\r\n    font-size: 14px;\r\n    font-weight: 600;\r\n    color: white;\r\n    flex: 1;\r\n    min-width: 0;\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    white-space: nowrap;\r\n  }\r\n  \r\n  .item-badges {\r\n    display: flex;\r\n    gap: 6px;\r\n    align-items: center;\r\n    flex-shrink: 0;\r\n    margin-right: 22px;\r\n  }\r\n\r\n  .discard-btn {\r\n    width: 18px;\r\n    height: 18px;\r\n    border-radius: 999px;\r\n    background: rgba(248, 113, 113, 0.9);\r\n    border: 1px solid rgba(248, 113, 113, 0.9);\r\n    color: white;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    transition: all 0.2s ease;\r\n    position: absolute;\r\n    top: 10px;\r\n    right: 10px;\r\n    z-index: 2;\r\n\r\n    &:hover {\r\n      transform: scale(1.08);\r\n      background: rgba(248, 113, 113, 1);\r\n    }\r\n\r\n    &:active {\r\n      transform: scale(1);\r\n    }\r\n\r\n    i {\r\n      font-size: 10px;\r\n      line-height: 1;\r\n    }\r\n  }\r\n  \r\n  .item-level {\r\n    padding: 2px 8px;\r\n    border-radius: 10px;\r\n    font-size: 10px;\r\n    font-weight: 600;\r\n    \r\n    &.rarity-c {\r\n      background: rgba(156, 163, 175, 0.2);\r\n      color: #d1d5db;\r\n    }\r\n    \r\n    &.rarity-b {\r\n      background: rgba(96, 165, 250, 0.2);\r\n      color: #93c5fd;\r\n    }\r\n    \r\n    &.rarity-a {\r\n      background: rgba(167, 139, 250, 0.2);\r\n      color: #c4b5fd;\r\n    }\r\n    \r\n    &.rarity-s {\r\n      background: rgba(251, 191, 36, 0.2);\r\n      color: #fcd34d;\r\n    }\r\n    \r\n    &.rarity-ss {\r\n      background: rgba(248, 113, 113, 0.2);\r\n      color: #fca5a5;\r\n    }\r\n  }\r\n  \r\n  .item-count {\r\n    font-size: 12px;\r\n    color: rgba(255, 255, 255, 0.5);\r\n    font-family: 'JetBrains Mono', monospace;\r\n  }\r\n  \r\n  .item-meta {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 8px;\r\n    margin-bottom: 6px;\r\n    flex-wrap: wrap;\r\n  }\r\n  \r\n  .item-type {\r\n    font-size: 11px;\r\n    color: rgba(255, 255, 255, 0.4);\r\n  }\r\n  \r\n  .item-slot {\r\n    font-size: 11px;\r\n    padding: 2px 8px;\r\n    border-radius: 6px;\r\n    background: rgba(102, 126, 234, 0.2);\r\n    color: #a78bfa;\r\n    font-weight: 500;\r\n  }\r\n  \r\n  .item-desc {\r\n    font-size: 12px;\r\n    color: rgba(255, 255, 255, 0.6);\r\n    line-height: 1.5;\r\n    margin-bottom: 8px;\r\n  }\r\n  \r\n  .equip-btn {\r\n    margin-top: 8px;\r\n    padding: 6px 12px;\r\n    border-radius: 8px;\r\n    background: linear-gradient(135deg, #667eea, #764ba2);\r\n    border: none;\r\n    color: white;\r\n    font-size: 12px;\r\n    font-weight: 500;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 6px;\r\n    transition: all 0.2s ease;\r\n    \r\n    &:hover {\r\n      transform: translateY(-2px);\r\n      box-shadow: 0 4px 12px rgba(102, 126, 234, 0.4);\r\n    }\r\n    \r\n    &:active {\r\n      transform: translateY(0);\r\n    }\r\n    \r\n    i {\r\n      font-size: 11px;\r\n    }\r\n  }\r\n}\r\n\r\n.empty-state {\r\n  padding: 40px 20px;\r\n  text-align: center;\r\n  \r\n  i {\r\n    font-size: 48px;\r\n    color: rgba(255, 255, 255, 0.1);\r\n    margin-bottom: 16px;\r\n  }\r\n  \r\n  p {\r\n    font-size: 14px;\r\n    color: rgba(255, 255, 255, 0.5);\r\n    margin-bottom: 4px;\r\n  }\r\n  \r\n  span {\r\n    font-size: 12px;\r\n    color: rgba(255, 255, 255, 0.3);\r\n  }\r\n}\r\n\r\n.tooltip {\r\n  position: fixed;\r\n  z-index: 10000;\r\n  background: rgba(20, 20, 30, 0.95);\r\n  backdrop-filter: blur(10px);\r\n  border: 1px solid rgba(255, 255, 255, 0.2);\r\n  border-radius: 12px;\r\n  padding: 12px;\r\n  min-width: 200px;\r\n  max-width: 300px;\r\n  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);\r\n  pointer-events: none;\r\n  \r\n  .tooltip-title {\r\n    font-size: 14px;\r\n    font-weight: 600;\r\n    color: white;\r\n    margin-bottom: 6px;\r\n  }\r\n  \r\n  .tooltip-level {\r\n    display: inline-block;\r\n    padding: 2px 8px;\r\n    border-radius: 6px;\r\n    font-size: 10px;\r\n    font-weight: 600;\r\n    margin-bottom: 8px;\r\n    \r\n    &.rarity-c {\r\n      background: rgba(156, 163, 175, 0.2);\r\n      color: #d1d5db;\r\n    }\r\n    \r\n    &.rarity-b {\r\n      background: rgba(96, 165, 250, 0.2);\r\n      color: #93c5fd;\r\n    }\r\n    \r\n    &.rarity-a {\r\n      background: rgba(167, 139, 250, 0.2);\r\n      color: #c4b5fd;\r\n    }\r\n    \r\n    &.rarity-s {\r\n      background: rgba(251, 191, 36, 0.2);\r\n      color: #fcd34d;\r\n    }\r\n    \r\n    &.rarity-ss {\r\n      background: rgba(248, 113, 113, 0.2);\r\n      color: #fca5a5;\r\n    }\r\n  }\r\n  \r\n  .tooltip-bonuses {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 4px;\r\n    margin-bottom: 8px;\r\n  }\r\n  \r\n  .tooltip-bonus-item {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    font-size: 12px;\r\n    color: rgba(255, 255, 255, 0.7);\r\n    \r\n    .positive {\r\n      color: #34d399;\r\n      font-weight: 600;\r\n    }\r\n    \r\n    .negative {\r\n      color: #f87171;\r\n      font-weight: 600;\r\n    }\r\n  }\r\n  \r\n  .tooltip-desc {\r\n    font-size: 11px;\r\n    color: rgba(255, 255, 255, 0.5);\r\n    line-height: 1.4;\r\n    margin-top: 8px;\r\n    padding-top: 8px;\r\n    border-top: 1px solid rgba(255, 255, 255, 0.1);\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
