ection-header {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 8px;\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  color: rgba(255, 255, 255, 0.8);\r\n  margin-bottom: 14px;\r\n  \r\n  i:first-child {\r\n    color: #667eea;\r\n  }\r\n  \r\n  .count-badge {\r\n    margin-left: auto;\r\n    padding: 2px 8px;\r\n    background: rgba(255, 255, 255, 0.1);\r\n    border-radius: 10px;\r\n    font-size: 11px;\r\n    font-weight: 500;\r\n    color: rgba(255, 255, 255, 0.6);\r\n  }\r\n}\r\n\r\n.present-list {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 10px;\r\n}\r\n\r\n.present-item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 10px;\r\n  padding: 10px 14px;\r\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.15), rgba(102, 126, 234, 0.05));\r\n  border: 1px solid rgba(102, 126, 234, 0.2);\r\n  border-radius: 12px;\r\n  \r\n  .present-avatar {\r\n    width: 32px;\r\n    height: 32px;\r\n    border-radius: 50%;\r\n    background: rgba(102, 126, 234, 0.3);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    overflow: hidden;\r\n    cursor: pointer;\r\n    transition: transform 0.2s ease;\r\n    \r\n    &:hover {\r\n      transform: scale(1.1);\r\n    }\r\n    \r\n    .avatar-img {\r\n      width: 100%;\r\n      height: 100%;\r\n      object-fit: cover;\r\n    }\r\n    \r\n    i, .fallback-icon {\r\n      font-size: 14px;\r\n      color: #a5b4fc;\r\n    }\r\n  }\r\n  \r\n  .present-name {\r\n    font-size: 13px;\r\n    font-weight: 500;\r\n    color: white;\r\n  }\r\n}\r\n\r\n.relationship-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 12px;\r\n}\r\n\r\n.relationship-card {\r\n  padding: 16px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 16px;\r\n  border: 1px solid rgba(255, 255, 255, 0.08);\r\n  position: relative;\r\n}\r\n\r\n.discard-btn {\r\n  width: 18px;\r\n  height: 18px;\r\n  border-radius: 999px;\r\n  background: rgba(248, 113, 113, 0.9);\r\n  border: 1px solid rgba(248, 113, 113, 0.9);\r\n  color: white;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  transition: all 0.2s ease;\r\n  position: absolute;\r\n  top: 12px;\r\n  right: 12px;\r\n  z-index: 2;\r\n\r\n  &:hover {\r\n    transform: scale(1.08);\r\n    background: rgba(248, 113, 113, 1);\r\n  }\r\n\r\n  &:active {\r\n    transform: scale(1);\r\n  }\r\n\r\n  i {\r\n    font-size: 10px;\r\n    line-height: 1;\r\n  }\r\n}\r\n\r\n.rel-header {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 14px;\r\n  margin-bottom: 16px;\r\n}\r\n\r\n.rel-avatar {\r\n  width: 48px;\r\n  height: 48px;\r\n  border-radius: 50%;\r\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.2));\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  overflow: hidden;\r\n  cursor: pointer;\r\n  transition: transform 0.2s ease;\r\n  \r\n  &:hover {\r\n    transform: scale(1.1);\r\n  }\r\n  \r\n  .avatar-img {\r\n    width: 100%;\r\n    height: 100%;\r\n    object-fit: cover;\r\n  }\r\n  \r\n  i, .fallback-icon {\r\n    font-size: 20px;\r\n    color: rgba(255, 255, 255, 0.6);\r\n  }\r\n}\r\n\r\n.rel-info {\r\n  flex: 1;\r\n}\r\n\r\n.rel-name {\r\n  font-size: 16px;\r\n  font-weight: 600;\r\n  color: white;\r\n  margin-bottom: 6px;\r\n}\r\n\r\n.rel-type {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  gap: 6px;\r\n  padding: 4px 10px;\r\n  border-radius: 12px;\r\n  font-size: 11px;\r\n  font-weight: 500;\r\n  \r\n  i {\r\n    font-size: 10px;\r\n  }\r\n  \r\n  &.type-stranger {\r\n    background: rgba(156, 163, 175, 0.15);\r\n    color: #d1d5db;\r\n  }\r\n  \r\n  &.type-classmate {\r\n    background: rgba(96, 165, 250, 0.15);\r\n    color: #93c5fd;\r\n  }\r\n  \r\n  &.type-friend {\r\n    background: rgba(52, 211, 153, 0.15);\r\n    color: #6ee7b7;\r\n  }\r\n  \r\n  &.type-lover {\r\n    background: rgba(244, 114, 182, 0.15);\r\n    color: #f9a8d4;\r\n  }\r\n  \r\n  &.type-master {\r\n    background: rgba(251, 191, 36, 0.15);\r\n    color: #fcd34d;\r\n  }\r\n  \r\n  &.type-submissive {\r\n    background: rgba(167, 139, 250, 0.15);\r\n    color: #c4b5fd;\r\n  }\r\n  \r\n  &.type-enemy {\r\n    background: rgba(248, 113, 113, 0.15);\r\n    color: #fca5a5;\r\n  }\r\n  \r\n  &.type-unknown {\r\n    background: rgba(156, 163, 175, 0.1);\r\n    color: rgba(255, 255, 255, 0.4);\r\n  }\r\n}\r\n\r\n.rel-stats {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 12px;\r\n}\r\n\r\n.stat-item {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 6px;\r\n}\r\n\r\n.stat-header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.stat-label {\r\n  font-size: 11px;\r\n  color: rgba(255, 255, 255, 0.5);\r\n}\r\n\r\n.stat-value {\r\n  font-size: 13px;\r\n  font-weight: 600;\r\n  font-family: 'JetBrains Mono', monospace;\r\n  \r\n  &.very-high { color: #f472b6; }\r\n  &.high { color: #34d399; }\r\n  &.medium { color: #60a5fa; }\r\n  &.low { color: #fbbf24; }\r\n  &.very-low { color: rgba(255, 255, 255, 0.4); }\r\n  &.training { color: #a78bfa; }\r\n}\r\n\r\n.stat-bar {\r\n  height: 6px;\r\n  background: rgba(255, 255, 255, 0.1);\r\n  border-radius: 3px;\r\n  overflow: hidden;\r\n}\r\n\r\n.stat-fill {\r\n  height: 100%;\r\n  border-radius: 3px;\r\n  transition: width 0.4s ease;\r\n  \r\n  &.affection {\r\n    &.very-high { background: linear-gradient(90deg, #ec4899, #f472b6); }\r\n    &.high { background: linear-gradient(90deg, #10b981, #34d399); }\r\n    &.medium { background: linear-gradient(90deg, #3b82f6, #60a5fa); }\r\n    &.low { background: linear-gradient(90deg, #f59e0b, #fbbf24); }\r\n    &.very-low { background: rgba(255, 255, 255, 0.2); }\r\n  }\r\n  \r\n  &.training {\r\n    background: linear-gradient(90deg, #8b5cf6, #a78bfa);\r\n  }\r\n  \r\n  &.submission {\r\n    &.very-high { background: linear-gradient(90deg, #ec4899, #f472b6); }\r\n    &.high { background: linear-gradient(90deg, #f59e0b, #fbbf24); }\r\n    &.medium { background: linear-gradient(90deg, #3b82f6, #60a5fa); }\r\n    &.low { background: linear-gradient(90deg, #10b981, #34d399); }\r\n    &.very-low { background: rgba(255, 255, 255, 0.2); }\r\n  }\r\n}\r\n\r\n.empty-state {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  padding: 60px 20px;\r\n  text-align: center;\r\n  \r\n  .empty-icon {\r\n    width: 80px;\r\n    height: 80px;\r\n    border-radius: 50%;\r\n    background: rgba(255, 255, 255, 0.03);\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-bottom: 20px;\r\n    \r\n    i {\r\n      font-size: 32px;\r\n      color: rgba(255, 255, 255, 0.15);\r\n    }\r\n  }\r\n  \r\n  .empty-title {\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    color: rgba(255, 255, 255, 0.5);\r\n    margin-bottom: 6px;\r\n  }\r\n  \r\n  .empty-desc {\r\n    font-size: 13px;\r\n    color: rgba(255, 255, 255, 0.3);\r\n  }\r\n}\r\n\r\n.reputation-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 10px;\r\n}\r\n\r\n.reputation-card {\r\n  padding: 12px;\r\n  background: rgba(255, 255, 255, 0.03);\r\n  border-radius: 12px;\r\n  border: 1px solid rgba(255, 255, 255, 0.08);\r\n}\r\n\r\n.rep-header {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 12px;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.rep-icon {\r\n  width: 36px;\r\n  height: 36px;\r\n  border-radius: 50%;\r\n  background: linear-gradient(135deg, rgba(102, 126, 234, 0.3), rgba(118, 75, 162, 0.2));\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  \r\n  i {\r\n    font-size: 16px;\r\n    color: rgba(255, 255, 255, 0.7);\r\n  }\r\n}\r\n\r\n.rep-info {\r\n  flex: 1;\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n}\r\n\r\n.rep-name {\r\n  font-size: 14px;\r\n  font-weight: 500;\r\n  color: white;\r\n}\r\n\r\n.rep-value {\r\n  font-size: 14px;\r\n  font-weight: 600;\r\n  font-family: 'JetBrains Mono', monospace;\r\n  \r\n  &.very-high { color: #34d399; }\r\n  &.high { color: #60a5fa; }\r\n  &.medium { color: rgba(255, 255, 255, 0.6); }\r\n  &.low { color: #fbbf24; }\r\n  &.very-low { color: #f87171; }\r\n}\r\n\r\n.rep-bar {\r\n  height: 4px;\r\n  background: rgba(255, 255, 255, 0.1);\r\n  border-radius: 2px;\r\n  overflow: hidden;\r\n}\r\n\r\n.rep-fill {\r\n  height: 100%;\r\n  border-radius: 2px;\r\n  transition: width 0.4s ease;\r\n  \r\n  &.very-high { background: linear-gradient(90deg, #10b981, #34d399); }\r\n  &.high { background: linear-gradient(90deg, #3b82f6, #60a5fa); }\r\n  &.medium { background: rgba(255, 255, 255, 0.3); }\r\n  &.low { background: linear-gradient(90deg, #f59e0b, #fbbf24); }\r\n  &.very-low { background: linear-gradient(90deg, #ef4444, #f87171); }\r\n}\r\n\r\n// \r\n.avatar-modal {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  z-index: 99999;\r\n  display: flex;\r\n  align-items: flex-start;\r\n  justify-content: center;\r\n  padding: 16px;\r\n  overflow-y: auto;\r\n  -webkit-overflow-scrolling: touch;\r\n}\r\n\r\n.modal-backdrop {\r\n  position: absolute;\r\n  top: 0;\r\n  left: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  background: rgba(0, 0, 0, 0.85);\r\n  backdrop-filter: blur(8px);\r\n}\r\n\r\n.modal-content {\r\n  position: relative;\r\n  background: linear-gradient(135deg, rgba(30, 30, 50, 0.98), rgba(20, 20, 40, 0.98));\r\n  border-radius: 20px;\r\n  border: 1px solid rgba(255, 255, 255, 0.15);\r\n  width: 360px;\r\n  max-width: 95vw;\r\n  max-height: 100%;\r\n  overflow: hidden;\r\n  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.6);\r\n  margin-top: 8px;\r\n  margin-bottom: 20px;\r\n  display: flex;\r\n  flex-direction: column;\r\n  \r\n  @media (min-height: 600px) {\r\n    margin-top: max(20px, 5vh);\r\n  }\r\n}\r\n\r\n.modal-close {\r\n  position: absolute;\r\n  top: 16px;\r\n  right: 16px;\r\n  width: 32px;\r\n  height: 32px;\r\n  border-radius: 50%;\r\n  background: rgba(248, 113, 113, 0.9);\r\n  border: none;\r\n  color: white;\r\n  cursor: pointer;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  z-index: 10;\r\n  transition: all 0.2s ease;\r\n  \r\n  &:hover {\r\n    transform: scale(1.1);\r\n    background: rgba(248, 113, 113, 1);\r\n  }\r\n  \r\n  i {\r\n    font-size: 14px;\r\n  }\r\n}\r\n\r\n.modal-header {\r\n  padding: 20px 24px;\r\n  border-bottom: 1px solid rgba(255, 255, 255, 0.1);\r\n  \r\n  h3 {\r\n    margin: 0;\r\n    font-size: 18px;\r\n    font-weight: 600;\r\n    color: white;\r\n  }\r\n}\r\n\r\n.modal-body {\r\n  padding: 24px;\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  min-height: 200px;\r\n  flex: 1;\r\n  min-height: 0;\r\n}\r\n\r\n.modal-avatar-img {\r\n  width: 100%;\r\n  max-height: 100%;\r\n  border-radius: 12px;\r\n  object-fit: contain;\r\n}\r\n\r\n.modal-fallback {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  gap: 12px;\r\n  color: rgba(255, 255, 255, 0.4);\r\n  \r\n  i {\r\n    font-size: 48px;\r\n  }\r\n  \r\n  p {\r\n    margin: 0;\r\n    font-size: 14px;\r\n  }\r\n}\r\n"],"sourceRoot":""}]);
